import axios from 'axios';
import { formatDate } from '../utils/formatters';

// URL de base pour les requtes API lies  l'entranement
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

/**
 * Service de gestion des donnes d'entranement
 */
class TrainingService {
  /**
   * Rcupre les activits d'un utilisateur pour une priode donne
   * @param {string} userId - Identifiant de l'utilisateur
   * @param {string} timeframe - Priode (week, month, year, all)
   * @returns {Promise<Object>} - Donnes des activits et rsum
   */
  async getActivities(userId, timeframe = 'month') {
    try {
      const response = await axios.get(`${API_URL}/training/activities`, {
        params: { userId, timeframe },
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      });
      
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des activits:', error);
      throw error;
    }
  }
  
  /**
   * Rcupre une activit spcifique
   * @param {string} activityId - Identifiant de l'activit
   * @returns {Promise<Object>} - Donnes dtailles de l'activit
   */
  async getActivityById(activityId) {
    try {
      const response = await axios.get(`${API_URL}/training/activities/${activityId}`, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      });
      
      return response.data;
    } catch (error) {
      console.error(`Erreur lors de la rcupration de l'activit ${activityId}:`, error);
      throw error;
    }
  }
  
  /**
   * Cre une nouvelle activit
   * @param {Object} activityData - Donnes de l'activit
   * @returns {Promise<Object>} - Activit cre
   */
  async createActivity(activityData) {
    try {
      const response = await axios.post(`${API_URL}/training/activities`, activityData, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      });
      
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la cration de l\'activit:', error);
      throw error;
    }
  }
  
  /**
   * Met  jour une activit existante
   * @param {string} activityId - Identifiant de l'activit
   * @param {Object} activityData - Nouvelles donnes
   * @returns {Promise<Object>} - Activit mise  jour
   */
  async updateActivity(activityId, activityData) {
    try {
      const response = await axios.put(`${API_URL}/training/activities/${activityId}`, activityData, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      });
      
      return response.data;
    } catch (error) {
      console.error(`Erreur lors de la mise  jour de l'activit ${activityId}:`, error);
      throw error;
    }
  }
  
  /**
   * Supprime une activit
   * @param {string} activityId - Identifiant de l'activit  supprimer
   * @returns {Promise<Object>} - Confirmation de suppression
   */
  async deleteActivity(activityId) {
    try {
      const response = await axios.delete(`${API_URL}/training/activities/${activityId}`, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      });
      
      return response.data;
    } catch (error) {
      console.error(`Erreur lors de la suppression de l'activit ${activityId}:`, error);
      throw error;
    }
  }
  
  /**
   * Importe une activit depuis un fichier GPX
   * @param {File} file - Fichier GPX
   * @param {Object} metadata - Mtadonnes de l'activit
   * @returns {Promise<Object>} - Activit importe
   */
  async importFromGpx(file, metadata = {}) {
    try {
      const formData = new FormData();
      formData.append('gpxFile', file);
      
      // Ajouter les mtadonnes au formulaire
      Object.keys(metadata).forEach(key => {
        formData.append(key, metadata[key]);
      });
      
      const response = await axios.post(`${API_URL}/training/import-gpx`, formData, {
        headers: { 
          Authorization: `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'multipart/form-data'
        }
      });
      
      return response.data;
    } catch (error) {
      console.error('Erreur lors de l\'importation du fichier GPX:', error);
      throw error;
    }
  }
  
  /**
   * Rcupre les statistiques d'entranement
   * @param {string} userId - Identifiant de l'utilisateur
   * @param {string} timeframe - Priode (week, month, year, all)
   * @returns {Promise<Object>} - Statistiques d'entranement
   */
  async getTrainingStats(userId, timeframe = 'month') {
    try {
      const response = await axios.get(`${API_URL}/training/stats`, {
        params: { userId, timeframe },
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      });
      
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des statistiques:', error);
      throw error;
    }
  }
  
  /**
   * Rcupre les objectifs d'entranement
   * @param {string} userId - Identifiant de l'utilisateur
   * @returns {Promise<Array>} - Liste des objectifs
   */
  async getTrainingGoals(userId) {
    try {
      const response = await axios.get(`${API_URL}/training/goals`, {
        params: { userId },
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      });
      
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des objectifs:', error);
      throw error;
    }
  }
  
  /**
   * Cre un nouvel objectif d'entranement
   * @param {Object} goalData - Donnes de l'objectif
   * @returns {Promise<Object>} - Objectif cr
   */
  async createTrainingGoal(goalData) {
    try {
      const response = await axios.post(`${API_URL}/training/goals`, goalData, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      });
      
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la cration de l\'objectif:', error);
      throw error;
    }
  }
}

// Exporter une instance du service
const trainingService = new TrainingService();
export default trainingService;
