import React from 'react';
import PropTypes from 'prop-types';
import MapComponent from '../cols/MapComponent';

/**
 * Adaptateur pour le composant RegionMap
 * Redirige vers le composant MapComponent existant pour maintenir la compatibilit
 * des importations dans la page d'accueil
 */
const RegionMap = (props) => {
  // Dfinir les coordonnes par dfaut pour la rgion Grand Est si non spcifies
  const defaultProps = {
    center: [48.7, 6.2], // Coordonnes approximatives de Nancy (centre du Grand Est)
    zoom: 7,
    ...props
  };
  
  return <MapComponent {...defaultProps} isRegionMap={true} />;
};

RegionMap.propTypes = {
  center: PropTypes.array,
  zoom: PropTypes.number,
  markers: PropTypes.array,
  routes: PropTypes.array
};

export default RegionMap;
